import emailjs from "@emailjs/browser";

const handleSendEmail = async (contact) => {

    try {
        const templateParams = {
            contact: contact,
        };

        await emailjs.send(
            'service_nc8eb5v',
            'template_u8x9818',
            templateParams, //   templateParams 
            'S8TdM4w0s5_wxoM_H'
        )
        // setStatus('  .    .')
        console.log(' ');
    } catch (error) {
        // setStatus(' .   .');
        console.error(' ', error.text);
    }
}

export default handleSendEmail;